import "./MainLine.less";
import info from "../../info";
import { FiMail, FiPhone } from "react-icons/fi";
import { BiMapPin } from "react-icons/bi";
import { SiLinkedin, SiGithub } from "react-icons/si";
import { Fragment } from "react";

const contacts = info.contacts;

const Contacts = () => (
  <Fragment>
    <div className="contacts">
      <span>
        <FiPhone /> {contacts.phone}
      </span>
      <span>
        <FiMail /> {contacts.email}
      </span>
    </div>

    <div className="contacts">
      <span>
        <SiGithub /> <a href={contacts.github.href} target="_blank">{contacts.github.name}</a>
      </span>
      <span>
        <SiLinkedin /> {contacts.linkedin}
      </span>
    </div>

    <div className="contacts">
      <span>
        <BiMapPin /> <a href={contacts.location.href} target="_blank">{contacts.location.name}</a>
      </span>
    </div>
  </Fragment>
);

const MainLine = () => {
  return (
    <div className="main-line">
      <div className="upper-color-line">
        <p className="main-name">{info.full_name}</p>
        <Contacts />
      </div>
    </div>
  );
};

export default MainLine;
